import {
  STORE_PARKING_INFO,
  STORE_USER_CAR_INFO,
  DELETE_CAR,
  FILTER_CARS,
  CLEAR_FILTER,
  ADD_AMT,
  CLEAR_USERS
} from '../types';

const UserCarReducer = (state, action) => {
  switch (action.type) {
    case STORE_PARKING_INFO:
      return {
        ...state,
        parkingData: action.payload
      };
    case STORE_USER_CAR_INFO:
      return {
        ...state,
        carData: [...state.carData,action.payload]
      };     
    case CLEAR_USERS:
      return {
        ...state,
        carData: []
      };  
    case DELETE_CAR:
      return {
        ...state,
        carData: state.carData.filter(
          car => car.carSlot !== action.payload
        ),
        filteredData: state.filteredData.filter(
          car => car.carSlot !== action.payload
        ),
      }; 
    case FILTER_CARS:
      return {
        ...state,
        filteredData: state.carData.filter(car => {
          const regex = new RegExp(`${action.payload}`, 'gi');
          return car.carNo.match(regex) || car.carColor.match(regex);
        })
      }; 
    case CLEAR_FILTER:
      return {
        ...state,
        filteredData: []
      };  
    case ADD_AMT:
      return {
        ...state,
        amt: state.amt + action.payload
      };                          
    default:
      return state;
  }
};


export default UserCarReducer