import React, { useReducer } from 'react';
import UserCarContext from './UserCarContext';
import UserCarReducer from './UserCarReducer';
import {
  STORE_PARKING_INFO,
  STORE_USER_CAR_INFO,
  DELETE_CAR,
  FILTER_CARS,
  CLEAR_FILTER,
  ADD_AMT,
  CLEAR_USERS,
} from '../types';



const UserCarState = props => {
  const initialState = {
    parkingData: {},
    carData:[],
    users: [],
    filteredData: [],
    amt: 10,
  };

  const [state, dispatch] = useReducer(UserCarReducer, initialState);


    // Store Parking Info of User
    const storeMainPgCount = text => {
  
      dispatch({
        type: STORE_PARKING_INFO,
        payload: text
      });
    };

    //Store Parking Info of User
    const storeUserCarData = text => {
  
      dispatch({
        type: STORE_USER_CAR_INFO,
        payload: text
      });
    };

    // Clear Users
    const clearUsers = () => dispatch({ type: CLEAR_USERS });
 
    //Delete Parking Info of User
    const deleteUserCarData = text => {
  
      dispatch({
        type: DELETE_CAR,
        payload: text
      });
    };  
    
    //Filter Parking Info of Users
    const filterCarData = text => {
  
      dispatch({
        type: FILTER_CARS,
        payload: text
      });
    };     

  // Clear Filter
  const clearFilter = () => {
    dispatch({ type: CLEAR_FILTER });
  };

    //Add Amount
    const addAmount = text => {
  
      dispatch({
        type: ADD_AMT,
        payload: text
      });
    };

  return (
    <UserCarContext.Provider
      value={{
        parkingData: state.parkingData,
        carData: state.carData,
        users: state.users,
        filteredData: state.filteredData,
        amt: state.amt,
        storeMainPgCount,
        storeUserCarData,
        deleteUserCarData,
        filterCarData,
        clearFilter,
        addAmount,
        clearUsers,
      }}
    >
      {props.children}
    </UserCarContext.Provider>
  );
};

export default UserCarState;
