import React, {useContext} from 'react';
import PropTypes from 'prop-types';
import UserCarContext from '../../context/usercar/UserCarContext';
import M from 'materialize-css/dist/js/materialize.min.js';

const CarTable = ({ car, id }) => {

  const userCarContext = useContext(UserCarContext);
  const {deleteUserCarData,addAmount} = userCarContext;  

  const onDelete = () => {
    deleteUserCarData(car.carSlot);
    addAmount(10);
    M.toast({ html: 'Car Deleted' });
  };


  let date = car.date.getDate() + '-' + (car.date.getMonth() + 1) + '-' + car.date.getFullYear();

  let time = car.date.getHours() + ':' + car.date.getMinutes() + ':' + car.date.getSeconds();
  
  
  return (
    <tr>
      <td className='center'>{id+1}</td>
      <td className='center'>{car.carNo}</td>
      <td className='center'>{car.carColor}</td>
      <td className='center'>{car.carSlot}</td>
      <td className='center'>{`${date}, ${time}`}</td>
      <td className='center'><a href="#parking-tool" className="btn yellow black-text center" onClick={onDelete}>
        Remove
        </a></td>
    </tr>
   

  );
};

CarTable.propTypes = {
  car: PropTypes.object.isRequired,
  id: PropTypes.number.isRequired
};

export default CarTable
