import React, { useState, useContext } from 'react';
import { useHistory } from "react-router-dom";
import UserCarContext from '../../context/usercar/UserCarContext';
import AlertContext from '../../context/alert/alertContext';

const MainParkPage = () => {
  const userCarContext = useContext(UserCarContext);
  const alertContext = useContext(AlertContext);
  let history = useHistory();
  const [text, setText] = useState({
    parkPlace: "",
    carParked: "",
  });

  const onSubmit = e => {
    e.preventDefault();
    let place = Number(text.parkPlace);
    let car = Number(text.carParked);

    if (place === '' || place === 0 || car === '' || car === 0) {
      alertContext.setAlert('Please fill all details', 'teal');
    } else if(place < 0 || car < 0){
      alertContext.setAlert('Please select positive values...', 'teal');
    } else if(place < car){
      alertContext.setAlert('Number of cars parked cannot be greater than number of parking places!!', 'teal');
    } else {
      userCarContext.storeMainPgCount(text);
      setText('');
      history.push('/parking-tool')
    }
  };

  const onChange = e => setText({
    ...text,
    [e.target.name]: e.target.value,
  });

  return (
    <div>
      <form onSubmit={onSubmit} className='form'>
        <div className='container'>
          <div className='card-panel'>
            <div className='row'>
              <div className="input-field col s12 m6">
                <h5>Number of parking places</h5>
                    <input
                      type='number'
                      name='parkPlace'
                      placeholder='Number of parking places'
                      value={text.parkPlace}
                      onChange={onChange}
                      maxLength='2'
                    />
              </div>
              <div className="input-field col s12 m6">
                <h5>Number of Cars Parked</h5>
                <input
                  type='number'
                  name='carParked'
                  placeholder='Number of Cars Parked'
                  value={text.carParked}
                  onChange={onChange}
                  maxLength='2'
                /> 
              </div> 
          </div>
          <div className='row'>
            <div className="col s12 m4 push-m4 pull-m4">
              <input
                type='submit'
                value='Submit'
                className='btn btn-dark btn-block'
              />
            </div>
          </div>
        </div>
      </div>      
      </form>
    </div>
  );
};

export default MainParkPage;
