import React, { Fragment , useContext, useState, useEffect, useRef} from 'react'
import UserCarContext from '../../context/usercar/UserCarContext';
import CarTable from './CarTable';
import M from 'materialize-css/dist/js/materialize.min.js';


const PARK = () => {
  const userCarContext = useContext(UserCarContext);

  const {parkingData,carData, clearUsers, filterCarData, filteredData, clearFilter, amt} = userCarContext;

  const hideClass = useRef();
  const typeRegNo = useRef();
  const typeColor = useRef();
  const carBtn = useRef();

  const [pkStateData, setpkStateData] = useState('');



  const checkAvailableSlots = () => {
      if(carData !== null && carData.length > 0){
        setpkStateData(parkingData.parkPlace - parkingData.carParked - carData.length);
      }else{
        setpkStateData(parkingData.parkPlace - parkingData.carParked);
      }
      if(pkStateData === 0 && (carBtn.current.classList.contains('disabled') === false)){
        carBtn.current.classList.add('disabled');
        M.toast({ html: `No Parking Slot is available. Please remove existing cars from parking!` });      
      }else{
        carBtn.current.classList.remove('disabled');
      } 
    }

    useEffect(()=>{
      checkAvailableSlots();
      //eslint-disable-next-line
    },[pkStateData,carData,parkingData.parkPlace,parkingData.carParked]);  
  
  
  useEffect(()=>{
      if(filteredData !== null && filteredData.length > 0){
        M.toast({ html: `${filteredData.length} search found!!` });      
      }
  },[filteredData]);

  const showAmount = () => {
    hideClass.current.classList.remove('hide'); 
    setTimeout(() => hideClass.current.classList.add('hide'),2000);
  };

  const search = () => {
      let colorValue = typeColor.current.value;
      let regValue = typeRegNo.current.value;

      if(colorValue !== null && colorValue !== ''){
        filterCarData(colorValue);        
      }
      if(regValue !== null && regValue !== ''){
        filterCarData(regValue);         
      }
      if((colorValue === null || colorValue ==='') && (regValue === null || regValue ==='')){
        clearFilter();     
      }        
    };

  return (
    <Fragment>
      <b><p>Total Parking Slots -  {parkingData.parkPlace}</p></b>
      <b><p>Available Parking Slots - {isNaN(pkStateData)?(0):(pkStateData)}</p></b>
      <div className='row'>
        <div className='col s12 m6 push-m3 pull-m3'>
          <div ref={hideClass} className='teal p-pt45 lighten-3 center z-depth-2 hide'>Total Money Collected is {amt}</div>
        </div>
      </div> 
      <div className='row'>
        <div className='col s12 m6'>
         <a href="#parking-tool" className="btn waves-effect waves-light black white-text" onClick={showAmount}>Get Amount</a>
        </div>
        <div className='col s12 m6'>
          <a id='bt1' ref={carBtn} className="btn waves-effect waves-light black white-text modal-trigger" href='#add-user-park-modal'
          >Park a Car</a>
        </div>
      </div>
      <div className='row'>
        <div className='input-field col s12 m4'>
          <input
          type='text'
          name='typeRegNo'
          ref={typeRegNo}
          placeholder='Type Registration Number'
          maxLength='20'
          />
        </div>
        <div className='col s12 m2'>
         <h5 className='center'>OR</h5>
        </div>
        <div className='input-field col s12 m4'>
        <input
          type='text'
          name='typeColor'
          ref={typeColor}
          placeholder='Type Color'
          maxLength='10'
          />
        </div>
        <div className='col s12 m2'>
         <a href="#parking-tool" className="btn blue white-text" 
         onClick={search}
         >Search</a>
        </div>
      </div>
      <div className='row'>
        <div className='col s12 m4 push-m4 pull-m4'>
         <a href="#parking-tool" className="btn waves-effect waves-light grey white-text" 
         onClick={()=> {
           clearUsers();
           M.toast({ html: `Added Parking data is reseted!` });
         }}>Reset</a>
        </div>
      </div>
      <div className='row'>
        <div className='col s12 m12'>
          <table className='highlight'>
            <thead>
              <tr className='blue-grey darken-4 white-text'>
                  <th className='center'>Serial No.</th>
                  <th className='center'>Car No</th>
                  <th className='center'>Color</th>
                  <th className='center'>Slot No.</th>
                  <th className='center'>Date Time</th>
                  <th></th>
              </tr>
            </thead>

            <tbody>
            {
              carData !== null && carData.length !== 0
              ? 
              filteredData !== null && filteredData.length !== 0
                ? 
                filteredData.map((car,index) => <CarTable car={car} id={index} key={index} />)
                : 
                carData.map((car,index) => <CarTable car={car} id={index} key={index} /> )
              : 
              (
                null
              )
            }
            </tbody>
          </table>
        </div>
      </div>
    </Fragment>
  )
}

export default PARK
